import styled from 'styled-components';
import {Link as LinkR} from 'react-router-dom';
import {Link as LinkS} from 'react-scroll';

export const Nav = styled.nav`
    background: ${props => props.theme.colors.lightBase};
    height: 80px;
/*     margin-top: -80px; */
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1rem;
    position: sticky;
    top: 0;
    z-index: 10;

    @media screen and (max-width: 960px) {
        transition: 0.8s all ease;
    }
`

export const NavbarContainer = styled.div`
    display: flex;
    justify-content: space-between;
    height: 80px;
    z-index: 1;
    width: 100%;
    padding: 0 24px;
    max-width: 1300px;
`

export const NavLogo = styled(LinkR)`
    color: ${props => props.theme.colors.darkText};
    justify-self: flex-start;
    cursor: pointer;
    font-size: 1.5rem;
    display: flex;
    align-items: center;
    margin-left: 4px;
    font-weight: bold;
    text-decoration: none;
`

export const ToggleIcon = styled.div`
    display: none;

    @media screen and (max-width: 768px) {
        display: block;
        position: absolute;
        z-index: 3;
        top: 0;
        right: 0;
        transform: translate(-100%, 60%);
        font-size: 1.8rem;
        cursor: pointer;
    }
`

export const NavMenu = styled.ul`
    display: flex;
    align-items: center;
    list-style: none;
    text-align: center;
    margin-right: -22px;

    @media screen and (max-width: 768px) {
        display: none;
    }
`

export const NavItem = styled.li = styled.li`
    height: 80px;
    display: flex;
    align-items: center;
    justify-content: center;
`

export const NavLink = styled(LinkR)`
    color: ${props => props.theme.colors.darkText};
    display: flex;
    align-items: center;
    text-decoration: none;
    padding: 0 1rem;
    height: 100%;
    cursor: pointer;

    &.active {
        border-bottom: 3px solid ${props => props.theme.colors.detail};
    }
`

export const NavBtn = styled.nav`
    display: flex;
    align-items: center;

    @media screen and (max-width: 768px) {
        display: none;
    }
`

export const NavBtnLink = styled(LinkR)`
    border-radius: 50px;
    white-space: nowrap;
    background: ${props => props.theme.colors.detail};
    padding: 10px 22px;
    color: ${props => props.theme.colors.lightText};
    border: none;
    outline: none;
    cursor: pointer;
    transition: all 0.2s ease-in-out;
    text-decoration: none;
    font-size: 16px;

&:hover {
    transition: all 0.2s ease-in-out;
    background: ${props => props.theme.colors.darkBase};
    color: ${props => props.theme.colors.lightText};
}
`


